body{
  font-family: $font-family;
}

.container {
    width: 1110px;
    margin: 0 auto;
    padding: 60px 0 80px 0;
    @include tablet {
      max-width: 100%;
      padding: 40px;
    }
    @include phone {
      padding: 0 15px;
    }
}

.header{
  background-color: $color-secondary;
  padding: 30px 5%;
  menu a{
    padding: 5px 10px;
    font-size:20px;
    line-height: 24px;
    font-weight: 300;
    color: #fff;
    transform: translate3d(0, 0, 0);
    &:after{
      content: "";
      display: block;
      position: absolute;
      top:35px;
      height: 3px;
      background:#fff;
      pointer-events: none;
      width: 0;
      transition: all 0.3s 0s cubic-bezier(0.455, 0.03, 0.515, 0.955);
    }
     &:hover{
       &:after{
         width: 80%;
       }
     }
  }
}

.footer{
  background-color: $color-secondary;
  font-size:16px;
  line-height: 24px;
  font-weight: 300;
  .footerNav li{
   padding-right:48px;
  }
  .social li{
   padding-left:32px;
  }
  a{
    color: #fff;
    &:hover{
      color:antiquewhite;
    }
  }
  .con{
    margin: 20px 0 24px 0;
    a{
     font-size:36px;
     line-height: 54px;
     margin-top: 16px;
    }
    img{
      margin-right:20px;
    }
  }
  .copyright{
    border-top: 1px solid #fff;
    padding-top: 32px;
    .privacyList{
      font-size:14px;
      line-height: 21px;
      li{
        padding-left:48px;
      }
    }
  }
}

// 共通元件
.title{
  font-size: 48px;
  line-height: 72px;
  font-weight: 900;
  text-align: center;
  margin-bottom: 40px;
  @include tablet {
    font-size: 32px;
    line-height: 48px;
  }
}


