/* text style */

.italic{
  font-style: italic;
}

.uppercase{
  text-transform:uppercase;
}

.text-white{
  color: white;
}

.text-black{
  color: black;
}

.text-primary{
  color: $color-primary;
}

.text-secondary{
  color: $color-secondary;
}

// 顯示

.block{
  display: block;
}

.hidden{
  display: none;
}

.inline-block{
  display: inline-block;
}

.m-auto{
  margin: auto;
}

.overflow-hidden{
  overflow: hidden;
}

// flex

.flex{
  display: flex;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.column{
  flex-direction: column;
}

.row-reverse{
  flex-direction:row-reverse;
}

.items-center{
  align-items: center;
}

.items-start{
  align-items: flex-start;
}

.justify-center{
  justify-content: center;
}

.justify-between{
  justify-content: space-between;
}
.justify-around{
  justify-content: space-around;
}

.justify-end{
  justify-content: flex-end;;
}

.content-start{
  align-content: flex-start;
}


